import { Component, ViewChild } from '@angular/core';
import { IonicPage, MenuController, NavController, Platform, ToastController, LoadingController, AlertController, ModalController } from 'ionic-angular';

import { Slides } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { GlobalVarsProvider } from '../../providers/global-vars/global-vars';
import { AlertServiceProvider } from '../../providers/alert-service/alert-service';
import { ClientdataProvider } from '../../providers/clientdata/clientdata';
import { RegisterPage } from '../register/register';
import { ValidatorProvider } from '../../providers/validator/validator'


@IonicPage()
@Component( {
  selector: 'page-login',
  templateUrl: 'login.html'
} )
export class LoginPage {
  dir: string = "ltr";
  @ViewChild( "slider" ) slider: Slides;
  slideIndex = 0;
  slides :any;

  error: number = 0;
  objectblock:any= {
    username: '', device_id: '0.08', lang: '',type:''
  };
  isPhoneSaved = false;
  relationship: any = "en"
  myValue: any;
  phoneError: boolean = false;
  encryptIsPresent = false;
  isCustomer: boolean = false;
  langs: any = {};
  language: any = 'en';
  light: any = 'light';
  pager: boolean = true;
  auto: any = '3000';
  isdisable = true;
  quick_list: any;

  constructor( public menuCtrl: MenuController, public platform: Platform, public validator: ValidatorProvider, private storage: Storage, private clientdata: ClientdataProvider, public navCtrl: NavController
    , public modalCtrl: ModalController, public mymenu: MenuController, public menu: MenuController,
    public globalVars: GlobalVarsProvider, public loadingController: LoadingController,
    public alertService: AlertServiceProvider, public alertCtrl: AlertController ) {
    this.langs = this.globalVars.doTranslate( this.globalVars.getLang() );
    this.language = this.globalVars.getLang();
    this.slides;
    this.quick_list;
    this.dir = platform.dir();
   
    this.load();
    this.storage.get( 'pbz' ).then(( val ) => {
      if ( val ) {
        this.globalVars.isLoggedIn = true;
        let decrypted = this.globalVars.doAESdecrypt( val, "9393099339" );
        console.log( decrypted );
        let dd = decrypted.split( "|" );
        this.globalVars.Username = dd[ 0 ];
        this.globalVars.trials = dd[ 1 ];
        this.isCustomer = this.globalVars.isLoggedIn;
        this.objectblock.username = this.globalVars.Username
       
      }

      this.objectblock.imsi= this.globalVars.simnumber;
      this.objectblock.lang = this.language
      this.objectblock.type = "slides"
      this.submit();

    } );
  

  }

  load() { 
    this.quick_list = [
      { name: this.langs.language, image: 'assets/svg/black/language.svg', link: 'lang' },
      { name: this.langs.forex, image: 'assets/svg/black/forex.svg', link: 'ForexPage' },
      { name: 'FAQs', image: 'assets/svg/black/faq.svg', link: 'FaqsPage' },
      { name: 'ATMs', image: 'assets/svg/black/atm.svg', link: 'AtmsPage' },
      { name: this.langs.contact_us, image: 'assets/svg/black/contact.svg', link: 'ContactPage' },
      { name: this.langs.notification, image: 'assets/svg/black/notification.svg', link: 'NotificationPage' },
      { name: this.langs.products, image: 'assets/svg/black/faq.svg', link: 'ProductsPage' },
    ];
  }

  showConfirm() {
    const confirm = this.alertCtrl.create( {
      title: this.langs.language,
      subTitle: this.langs.preferred_lang_txt,
      cssClass: 'popa',
      buttons: [
        {
          text: 'ENGLISH',
          handler: () => {
            this.globalVars.setLang( 'en' );
            this.langs = this.globalVars.doTranslate( this.globalVars.getLang() );
            this.load();
          }
        },
        {
          text: 'KISWAHILI',
          handler: () => {
            this.globalVars.setLang( 'ki' );
            this.langs = this.globalVars.doTranslate( this.globalVars.getLang() );
            this.load();
          }
        }
      ]
    } );
    confirm.present();
  }


  popmeth( message ) {
    let template = "<div>" + message + "</div>";
    let obj = { body: "", template: template, ttype: "", completed: true, pageTo: '' };
    let myModal = this.modalCtrl.create( 'ConfirmModalPage', obj );
    myModal.present();
    myModal.onDidDismiss( data => {
      if ( data ) {
        this.alertService.directExit();
      }
      else {
        // this.alertService.directExit();
      }
    } );
  }
  ionViewDidEnter() {
    this.globalVars.pause();

  }

  submit() {
    console.log( this.objectblock );
    this.clientdata.faq( this.objectblock )
      .subscribe( data => {
        data = this.globalVars.testdec( data );
        console.log( data )
        if ( data.length != 0 ) {
          if ( !data.error ) {
            this.slides = data.data;
            // this.slides.splice( 0, 1 );
            // this.slider.initialSlide
          } else if ( data.error ) {
            this.alertService.mytoastdown( this.langs.tech_error );
          } else {
            this.alertService.mytoastdown( this.langs.tech_error );
          }
        } else {
          this.alertService.mytoastdown( this.langs.tech_error );
        }
      }, err => {
        this.alertService.errorPop( this.langs.internet );
      } );
  }

  changelang( langus ) {
    console.log( langus );
    if ( langus == 'ki' ) {
      this.globalVars.setLang( 'ki' );
      this.langs = this.globalVars.doTranslate( this.globalVars.getLang() );
    }
    else if ( langus == 'en' ) {
      this.globalVars.setLang( 'en' );
      this.langs = this.globalVars.doTranslate( this.globalVars.getLang() );
    }
  }

  goRegister() {
    this.navCtrl.push( 'RegisterPage' );
  }

  openPage( ppage ) {
    if ( ppage == 'lang' ) {
      this.showConfirm();
    } else{
    this.navCtrl.push( ppage );
  }
  }

  userNameChanged() {
    if ( this.globalVars.isLoggedIn ) {
      this.alertService.showPin();
    } else { 
      this.navCtrl.push('OtpPage');
    }
  }

  
}



